body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important; }

img {
  vertical-align: middle; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

.Home-enter-active,
.Home-leave-active,
.Start-enter-active,
.Start-leave-active,
.FortuneText-enter-active,
.FortuneText-leave-active,
.FortuneContent-enter-active,
.FortuneContent-leave-active {
  -webkit-transition: opacity 2s ease-in-out;
  transition: opacity 2s ease-in-out; }

.Home-enter,
.Home-leave-to,
.Start-enter,
.Start-leave-to,
.FortuneText-enter,
.FortuneText-leave-to,
.FortuneContent-enter,
.FortuneContent-leave-to {
  opacity: 0; }

* {
  font-family: "Noto Serif TC", serif; }

.button_boxAnime {
  height: 0 !important; }

.Fortune_page {
  position: fixed;
  width: 100%; }

#Fortune {
  position: fixed;
  height: 100%;
  width: 100%;
  background-image: radial-gradient(rgba(0, 0, 0, 0.35), black), url(../../assets/images/fortune_bg.jpg);
  background-size: 100% 100%;
  background-position: center center;
  background-repeat: no-repeat; }
  #Fortune .Home {
    position: relative;
    padding: 0 8%;
    min-height: 100%; }
    #Fortune .Home h1 {
      position: absolute;
      right: 20%;
      line-height: 120px;
      width: 120px;
      color: white;
      font-size: 120px;
      margin-top: 50px; }
      #Fortune .Home h1 p {
        font-size: 120px; }
    #Fortune .Home h2 {
      font-size: 24px;
      color: white;
      font-weight: 900;
      margin-bottom: 24px; }
    #Fortune .Home p {
      font-size: 16px;
      color: white;
      margin: 5px 0; }
    #Fortune .Home .Fortune_step {
      margin-bottom: 40px;
      padding-top: 15%; }
    #Fortune .Home ul {
      width: 370px; }
      #Fortune .Home ul li {
        display: inline-block;
        width: calc(100% / 3 - 16px);
        margin: 8px;
        border: 1px white solid;
        color: white;
        font-size: 16px;
        text-align: center;
        padding: 10px;
        cursor: pointer; }
        #Fortune .Home ul li:hover {
          color: #000;
          background-color: white; }
        #Fortune .Home ul li.active {
          color: #000;
          background-color: white; }
  #Fortune .button_box {
    height: 130px;
    width: 100%;
    background-color: white;
    padding-left: 180px;
    -webkit-transition: height 2s ease;
    transition: height 2s ease; }
    #Fortune .button_box .box_start {
      color: #000;
      font-size: 24px;
      font-weight: bold;
      margin-top: 40px;
      width: 355px;
      height: 55px;
      border: 2px #000 solid;
      text-align: center;
      line-height: 55px;
      -webkit-transition: all 0.8s ease;
      transition: all 0.8s ease;
      cursor: pointer; }
      #Fortune .button_box .box_start:hover {
        color: white;
        background-color: #000; }

.Start {
  position: relative;
  height: calc(100% - 188px);
  padding: 0 8%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }
  .Start .Start_border {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 40px;
    z-index: 1; }
    .Start .Start_border .line {
      position: absolute;
      background-color: rgba(255, 255, 255, 0.3); }
      .Start .Start_border .line:nth-of-type(1) {
        width: calc(100% - 250px);
        height: 1px;
        top: 6%;
        left: 6%; }
      .Start .Start_border .line:nth-of-type(3) {
        width: calc(100% - 250px);
        height: 1px;
        bottom: 6%;
        left: 6%; }
      .Start .Start_border .line:nth-of-type(2) {
        width: 1px;
        height: calc(100% - 200px);
        top: 13%;
        left: 3.5%; }
      .Start .Start_border .line:nth-of-type(4) {
        width: 1px;
        height: calc(100% - 200px);
        top: 13%;
        right: 3.5%; }
    .Start .Start_border p {
      position: absolute;
      color: rgba(255, 255, 255, 0.4);
      font-size: 36px;
      font-weight: 900; }
      .Start .Start_border p:nth-of-type(1) {
